<template>
  <div style="position: relative; height: 420px; width: 420px;">
    <!-- multiple canvases allow for a buffer layer -->
    <canvas
      v-for="(can, idx) in 2"
      :key="idx"
      width="420"
      height="420"
      ref="can" />
  </div>
</template>
<script>
import EventBus from '../Eventbus';
export default {
  name: 'BCan',
  props: {
    curPos: {
      type: Object,
      default: function(){
        return null;
      }
    },
    lineColor: {
      type: String,
      default: null
    },
    color: {
      type: String,
      default: "000000"
    }
  },
  data: function(){
    return{
      points: [],
      strokes: [],
      frames: [],
      curFrame: null,
      frameNum: 0,
    }
  },
  watch:{
    curPos:{
      deep: true,
      handler(point){
        this.clear(1);
        if(this.color || this.lineColor){
          if(point){
            this.points.push(point);
            this.drawStroke(1, this.points, this.color, this.lineColor);
            // this.stroke.push({points: this.points, color: this.color, lineColor: this.lineColor});
          }else{
            // pointer up.
            this.drawStroke(0, this.points, this.color, this.lineColor);
            this.points = [];
          }
        }
      }
    }
  },
  methods: {
    clearAll(){
      this.frames = [];
      this.strokes = [];

      for(const i in this.$refs.can){
        this.clear(i);
      }
    },
    clear(idx){
      const can = this.$refs.can[idx];
      can.getContext('2d').clearRect(0, 0, can.width, can.height)
    },
    save(){
      //
    },
    drawFrame(n){
      if(this.frames[n]){
        this.frames[n].forEach((_stroke) => {
          this.draw(0, _stroke);
        })
      }
    },
    drawStroke(idx, path, col, lCol){
      const ctx = this.$refs.can[idx].getContext('2d');

      if(lCol){
        ctx.strokeStyle = '#'+lCol;
      }

      if(col){
        ctx.fillStyle = '#'+col;
      }

      if(idx == 1){
        ctx.globalCompositeOperation = 'xor';
      }

      if(path){
        ctx.beginPath();
        ctx.moveTo(path[0].x, path[0].y);
        path.forEach((point, i) => {
          ctx.lineTo(point.x, point.y);
        });
        ctx.lineWidth =  3;

        if(col){
          ctx.fill();
          if(!lCol){
            ctx.closePath();
          }
        }

        if(lCol){
          ctx.stroke();
        }
      }
    },
    draw(_idx, strokes){
      const ctx = this.$refs.can[_idx].getContext('2d');

      if(_idx == 0){
        this.clear(0);
      }

      // console.log(strokes);

      strokes.forEach((_stroke, idx) => {
        this.drawStroke(ctx, _idx, _stroke.stroke, _stroke.color, _stroke.lineColor);
      });
    },
    frameChange(v){
      // this.curFrame = this.frames[v];
      this.clearAll();

      if(this.points.length){
        const pt = this.points.shift();
        this.points = [pt];
      }
    }
  },
  mounted(){
    // this.curFrame = this.frames[this.frameName];
    EventBus.$on('frameChange', this.frameChange);
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
canvas{
  position: absolute;
  top: 0px;
  left: 0px;
  }
</style>
